/* site manager shim: apply legacy styles */
#main-container #tabArchitectural{
    // BS2 Lists
    @horizontalComponentOffset: 180px;
    ul, ol {
      padding: 0;
      margin: 0 0 @baseLineHeight / 2 25px;
    }
    ul ul,
    ul ol,
    ol ol,
    ol ul {
      margin-bottom: 0;
    }
    li {
      line-height: @baseLineHeight;
    }
  }
/* /site manager shim */

/* site manager architectural layout */
#carch #mura-content #sitemgr-reorder{
  clear: both;
}
#sm-modify-view .btn-group{
  .pull-right;
}
#sm-modify-view .dropdown-menu{
  .pull-right;
  .dropdown-menu-right;
}
#gridContainer .btn-group,
#tabArchitectural .btn-group,
#sm-modify-view{
  float: right;
  margin: 0 0 15px 0;
}

/* 
  see custom-responsive.less for breakpoint-specific styles 
*/
#tabArchitectural #gridContainer{
  min-width: 100%;
  max-width: 100%;
}
#tabArchitectural #gridContainer dl dt:first-child {
  width: ~'calc(100% - 160px)' !important;
}
#tabArchitectural #gridContainer .mura-grid-hdr span.add{
  min-width: 24px;
  min-height: 24px;
}
#carch #mura-content #tabArchitectural ul.breadcrumb,
#carch #mura-content #gridContainer ul.breadcrumb{
  float: left;
}
#tabArchitectural #gridContainer dl {
  width: 100%;
  float: none;

  &:after{
    clear: both;
    content: '';
    display: block;
  }
}
#tabArchitectural #gridContainer dl.mura-grid-hdr {
  overflow: hidden;
}
#main-container #tabArchitectural .mura-section {
  /* 
    overflow: hidden; 
  */
  margin-bottom: 0 !important;
}
dd.display, dd.nav, dd.objects, dd.template {
  display: none;
}
.mura-grid li {
  float: none;
}
.mura-grid #mura-nodes{
  margin-bottom: 0 !important;
}
#mura-nodes li li .title {
  float: none;
  max-width: none;
}
#mura-nodes-clear{
  float: right;
  margin: 0 8px;
}

/* /site manager architectural */

/* site manager flat layout */
#carch #mura-content #flatViewContainer .mura-table-grid > tbody > tr > td,
#carch #mura-content #flatViewContainer .mura-table-grid > tbody > tr > th,
#carch #mura-content #flatViewContainer .mura-table-grid > tfoot > tr > td,
#carch #mura-content #flatViewContainer .mura-table-grid > tfoot > tr > th,
#carch #mura-content #flatViewContainer .mura-table-grid > thead > tr > td,
#carch #mura-content #flatViewContainer .mura-table-grid > thead > tr > th{
  border: none !important;
}

#carch #mura-content #flatViewContainer .breadcrumb-label{
  float: left;
  font-size: 11px !important;
  margin-right: 6px;
  margin-top: 4px;
}

#carch #mura-content #flatViewContainer .nodeMeta{
    padding: 4px 0;
  }

#carch #mura-content #flatViewContainer .thumbnail{
  .pull-left;
}


#carch #mura-content #flatViewContainer ul.breadcrumb,
#carch #mura-content #flatViewContainer ol.breadcrumb{
  clear:none;
  margin-left: 0;  
  text-transform: none;
  > li:last-child:not(:first-child){
    display:none;
  }
  > li{
    margin-top:3px;
  }
  > li strong a,
  > li a{
    font-size: 11px !important;
    font-weight: normal;
    margin-top: 3px;
    padding-right: 7px;
  }
}
#carch #mura-content #flatViewContainer ul.breadcrumb > :first-child,
#carch #mura-content #flatViewContainer ol.breadcrumb > :first-child{
  margin-left: 0 !important;
}

#carch #mura-content #flatViewContainer .search-showing{
  .pull-left;
  clear: left;
  padding-top: 4px;
}

#carch #mura-content #flatViewContainer .pagination{
  .pull-right;
}

/* /site manager flat layout */

/* content objects tab */
#editObjects {
  
  > div {
    .half;
  }
    
    .region {
      .clearfix;
      }
    
    dl {
      .pull-left;
      margin: 0 6px 10px 0;
      padding: 0 !important;
      
      dt {
        margin-bottom: .5em;
        font-weight: normal;
        font-style: italic;
      }
      
      dd {
        margin: 0;        
        }
      }
    
    #availableRegions dl {
      margin-left: 6px;
    }
    
    .btn-group {
      .pull-left;
      height: 100px;
      padding-top: 50px;
      }

    .multiSelect {
      min-height: 100px;
      height: auto;
      width: 300px;
      margin: 0;
    }
  
  .btn {
    width: 6px;
    padding: 4px 10px;
    
    i {
      position: relative;
      top: 2px;
      font-size: 12px;
      text-shadow: 1px 0 0 @white;
    }
  }
}

#availableObjectsContainer {
  
  select#availableObjects {
    width: 270px;
    max-width: 270px;
  }
  
  .control-group {
    padding: 0;
  }
  
  #classList {
    clear: both;
  }
  
}

#availableRegions .btn-group-vertical .btn{
  border-radius:0;
  padding: 4px 13px 4px 10px;
  margin: 0 2px;
}

/* /content objects tab */ 

/* list display options tab */
#configuratorContainer {

  h2 {
    margin-top: -@baseLineHeight;
    }
  
  .form-actions {
    padding-top: @baseLineHeight;
    text-align: center;
    
    .btn {
      .btn-default;
      }
    }
  }

#availableFields, #availableGroups {
  margin-bottom: 0;
}

#availableFields span, #availableGroups span, .dragMsg span {
  .inline-block;
  color:#404040;
  }

#sortableFields, .sortableFields, #sortableGroups, .sortableGroups {
  
  .clearfix;
  clear: both;

  .dragMsg {
    font-style: italic;
    color: @gray;
    overflow: hidden;
    margin: 0 0 @baseLineHeight/2;
    line-height: 1;
    height: @baseLineHeight;
    padding: 0 0 16px 0;
    .clearfix;

      .half + .half{
        padding-left: 6px;
      }
    }

  li, li.ui-sortable-helper {

    background: none;
    background-color: #b1b1b1;
    border: 0;
    border-radius:1px;
    cursor: move;
    color: #FFF;
    margin: 0 0 3px 0;
    overflow: hidden;
    padding: 6px 6px 6px 8px;
    text-overflow: ellipsis;
    text-shadow: none;
    }

  #availableListSort,
  #displayListSort,
  #contentAvailableListSort,
  #contentDisplayListSort,
  #groupAvailableListSort,
  #groupAssignmentListSort  {
    .box-sizing(border-box);
    margin: 0 0 @baseLineHeight !important;
    padding: 6px 0 !important;
    list-style-type: none;
    border: none !important;
    width: 48%;
    float: right;
  }
  
  #availableListSort,
  #contentAvailableListSort,
  #groupAvailableListSort {
    border: 1px dotted @grayLight;
    min-height: 36px;
    margin: 0 18px 0 0;
    float: left;
    
    li {
      margin-left: 0;
    }
  }
  
  #contentDisplayListSort, #displayListSort, #groupAssignmentListSort{
    
    li {
      background-color: #545454;
      color: #FFF;
     /* background: hsl(55,80%,88%);*/
    }
    
  }
}

#sortableGroups > ol#groupAssignmentListSort {
  
  list-style: inside decimal; 

}
/* /list display options tab */

