/* associated files */
#mura-content-body-render #assocFileContainer{
    height: ~'calc(100vh - 326px)';
    margin-right: -25px;
    margin-top: -25px;
    overflow-y: auto;
    padding-bottom: 35px;
    padding-right: 15px;
    padding-top: 25px;

    #msg-file-locked + #assocFileSelectorContainer{
      clear:both !important;
    }

}
.mura-assoc-marker > i{
    font-size: 16px;
    margin-right: 4px;
    position: relative;
    top: 1px;
}
.selectAssocImageResults {  
  background-color: transparent;  

  h3{
    margin-bottom: 6px;
    font-size: 14px;
  }
  .assocfile-panels{
    clear: left;
    margin-top: 8px;

    .mura-panel {
      margin-bottom: 0 !important;
      clear: left;

    .assets-divider{
      font-size: 13px;
      font-weight: 700;
      margin: 6px 5px 12px;
      display: block;
      background: #f4f3f3;
      padding: 8px 10px;
    }

     .mura-panel-heading .mura-panel-title a{

       &:before{
          content: "\f107";
          color: @textcolor;
          display: inline-block;
          position: absolute;
          font-family: FontAwesome;
          font-size: 15px;
          font-weight: 700;
          top: 50%;
          right: 12px;
          transform: translateY(50%);
          -webkit-transform: translateY(-50%);
          -ms-transform: translateY(-50%);
      }

      &.collapsed:before {
          content: "\f105";
          right: 16px;
      }
    } /* /panel-title */
    
    .panel-collapse {
      overflow-y: auto;
      background-color: #FFF;
      
      > .mura-panel-body{
/*        max-height: 250px;
        overflow-y: scroll;
*/        padding: 12px 12px 0;

        > ul { 
          list-style: none;
          margin-bottom: 0;

          > li {
            border: none;
            border-radius:2px;
            background: #fff;
            cursor: pointer;
            .clearfix();

             input[type=radio]{
              cursor: pointer;
              margin-top: 0;
              position: relative;
              right: 3px; 
              top: 2px;
             } 

            }        
          }
      }
        
      

      }
    } /* /.mura-panel */

  /* images results list */
  .mura-panel.mura-assoc-looseimages .mura-panel-body > ul { 
    display: flex;
    flex-direction:  row;
    flex-wrap: wrap;
    justify-content: left;
    padding-bottom: 10px;

     &::after {
      content: "";
      flex: auto;
    }

   > li {
      .inline-block();
      text-align: center;     
      cursor: pointer; 
      margin: 5px;
      
      &.active:after{
        box-shadow: inset 0 0 0 4px @mura-red;
        content: '';
        display: block;
        height: 100%;
        position: absolute;
        top: 0;
        width: 100%;
      }
    
       input[type=radio]{              
        margin-top: 8px !important; 
        cursor: pointer;
       } 
       &.hasImage{

        height: 80px;
        width: 80px;
        position: relative;
        
        img{
          margin: auto;
          width: 80px;
          height: 80px;
          object-fit: cover;
          position: absolute;
          top: 0;
          bottom: 0;
          left: 0;
          right: 0;
        }

       input[type=radio]{              
        display: none;
       }
      }
    }
  } /* /.mura-assoc-images */

  .mura-panel.mura-assoc-images .mura-panel-body {
    > hr {
      border: 1px solid #f0f0f0;
      margin: -3px 0 5px 0;
      padding: 0;
    }
    > span {
      font-size: 10px;
    }
  }

  .mura-panel.mura-assoc-images .mura-panel-body > ul { 
          display: flex;
          flex-direction:  row;
          flex-wrap: wrap;
          justify-content: left;
          padding-bottom: 10px;

           &::after {
            content: "";
            flex: auto;
          }

         > li {
            .inline-block();
            text-align: center;     
            cursor: pointer; 
            margin: 5px;
            
            &.active:after{
              box-shadow: inset 0 0 0 4px @mura-red;
              content: '';
              display: block;
              height: 100%;
              position: absolute;
              top: 0;
              width: 100%;
            }
          
             input[type=radio]{              
              margin-top: 8px !important; 
              cursor: pointer;
             } 
             &.hasImage{

              height: 80px;
              width: 80px;
              position: relative;
              
              img{
                margin: auto;
                max-width: 80px;
                width: 80px;
                height: 80px;
                object-fit: cover;     
                position: absolute;
                top: 0;
                bottom: 0;
                left: 0;
                right: 0;
              }

             input[type=radio]{              
              display: none;
             }
            }
          }
        } /* /.mura-assoc-images */
  
    .mura-panel.mura-assoc-files .mura-panel-body {
      > hr {
        border: 1px solid #f0f0f0;
        margin: -3px 0 5px 0;
        padding: 0;
      }
      > span {
        font-size: 10px;
      }
    }

    /* files results list */
    .mura-panel.mura-assoc-files .mura-panel-body > ul{
       
       padding: 0 0 12px;

       > li {
            cursor: pointer; 
            line-height: 28px;
            padding: 3px 0 0 5px;
            text-align: left;

            input[type="radio"] {
              cursor: pointer;
              margin: 10px 3px 0 0!important;
              float: left;
              top: -1px;
              left: 0px;
              right: 0;

              }
            
            i {
              vertical-align: middle;
              margin: 0 4px;
              }
            } 
      } /* /.mura-assoc-files */

  } /* /assocfile-panels */
 
} /* /associated files */



