/* Multi-File Upload */
#fileupload {
  
  #fileupload-target {
    margin-bottom: @baseLineHeight;
    padding: 24px;
    
    &:before {
      content: "";
    }
    
    p {
      text-align: center;
      margin-bottom: 0 !important;
      
      i {
        font-size: 72px;
        color: darken(@blue,10%);
        text-shadow: 0 1px 1px rgba(255,255,255,.65);
        display: block;
        margin: @baseLineHeight;
      }
    }
  }
}
#multi-file-upload {

  .fileupload-buttonbar {
     .btn{
        padding-top: 6px;
        
        > i {
            font-size: 16px;  
          }
    
     }

    }
  
  .progress {
      margin: 0;
      height: 28px;
      }
  
  .fileupload-progress {
    
    .progress-extended {
        margin: @baseLineHeight/4 0;
      } 
  }
  
  table {

    margin-bottom: 0;
    border: none !important;
    
    td {
    
      vertical-align: top;
      padding: 12px !important;
      
      .btn.mura-edit-file{
        font-size: 14px;
      }

      &.file-preview {
        
        img, canvas {
          .img-rounded;
          border-radius: 2px;
          margin-bottom: 4px;
          width: 60px;
          max-width: 60px;
          height: auto;
          }
          
        > .preview {
        
          position: relative;
          
          > i {
            font-size: 64px;
            margin-bottom: 0;
            width: 1em;
            
            &:before {
              
            }
          }
        
          > .badge {
            background-color: #000 !important;
            border-radius:2px;
            .center-block;
            color: #FFF;
            font-size: 10px;
            font-weight: normal;
            margin-top: 12px;
            padding: 3px 0 !important;
            text-align:center;
            width: 32px;
            }
          }
        }
                
        
      .mura-control-group {       
          
        >  label {
            float: left;
            min-width: 120px;
            width: 20%;
          }

        > label + div{
          width: 75%;
          float: left;
        }
                  
        div {
          line-height: 1.2;
          min-height: 32px;
          padding: 8px;
          border:1px solid #e6e6e6;
          border-radius: 2px !important;
          
          &[data-attribute="summary"] {
            min-height: 56px;
            overflow-y: auto;
          }
      
          &.editable {
            background: @grayLighter;
            }
            
            p {
              margin: 0;
              line-height: 1.2;
            }     
          }
        }
      
      > .progress {
        margin-top: 1px;
        width: 160px;       
        .center-block;
        
        + p {
          line-height: 2;
          }
        }
      
      .btn-group > .btn > i {
            font-size: 20px;
            vertical-align: -2px;
            }
          
      .size {
          clear:both;
          display:block;
          padding-top: 8px;
        }
        
      }
      
    tr:nth-child(odd) > td .controls > .editable {
      background-color: darken(#ebf4fe,3%);
      }
      
    }
    
  }
/* /Multi-file Upload */