@iconColor: lighten(@grayDark,18%);

#carch h1 + .btn-group {
	/* clear: both; */
}

#carch .tab-content {
	overflow: visible;
}

#filter-view {
	margin: 0 0 @baseLineHeight 0;
}

#sm-modify-view {
	margin-top: -28px !important;

	.dropdown-menu {

		padding: 10px;
		min-width: 112px;
		
		.mura-control-group{
			margin-bottom: 3px;
		  overflow: hidden;
	    white-space: nowrap;
		}

		.mura-control-group + .mura-control-group{
			margin-top: 12px;
		}
		form {
			margin-bottom: 0;
		}

		label{		
			display: block;
			margin-bottom: 0;

			select {
				.form-control;
				cursor: pointer;
				display: inline-block;
				height: 24px;
				margin-bottom: 0;
				margin-top: 8px;
		    padding: 0px 4px;
				width: 100%;
			}

			input.text {
				margin: 0 0 0 4px;
				padding: 2px 4px;
				height: 24px;
			}

			.numeric{
				max-width: 4em;
			}

		}

		label + select {
			margin-top: 4px;
		}

		.btn {
			margin-top: 12px;
		}

	}

}


#sitemgr-reorder {

	margin-top: 1em;

	.btn {
	/*	margin-left: 1em;*/
	}
}

/* content, staging search filters */
#navFilters{
	background-color: #F5F5F5;
	margin: 12px 0 24px 0;
	min-height: 40px;
}

#navFiltersToggle{
	cursor: pointer;
	padding-top: 11px;
	text-align: center;

		> i{
			font-size: 11px;
			margin-left: 4px;
		}

}

#navFilterButtons{
	background-color: transparent;
	bottom: 0px;
	box-shadow: none !important;
	height: 44px;
	float: right;
	margin: 24px 0 0 !important;
	min-height: 44px;
	min-width: 160px;
	padding: 0 !important;
	text-align: right;
	position: relative;
	width: auto;

	.btn{
		margin-right: 12px;
	}
	.form-actions{
		clear:both;
		text-align: right;
	}

}

#navFilterControls{
	border-top: 1px solid #E4E4E4;
  padding: 13px 0px;
  margin: 12px 20px;

  	.mura-layout-row{
  		margin-right: -12px;
  	}
		.mura-control-group{
			float: left;
			margin-bottom: 12px;
  		padding-right: 12px;
		}

  	.mura-control-group input[type=text],
  	.mura-control-group label,
  	.mura-control-group select{
  		width: 100% !important;
  	}
  	.mura-control-group label{
  		overflow:hidden;
  		text-overflow: ellipsis;
  		white-space: nowrap;
  	}

  	.tagSelector input{
  		margin-top: 0;
  	}
  	.tagSelector .tag{
  		border-radius: 2px;
  		height: 22px;
  	}
  	.tagSelector .tag a i{
  		color: @white;
	    float: right;
  		font-size: 11px;
	    font-weight: normal;
	    margin-top: 3px;
	    margin-left: 6px;
  	}

  	#category-select-control{
			border-radius: 3px;
			color: @textcolor;
	    display: block;
	    line-height: 1.6;
	    padding: 6px 15px;
	    height: 36px;
	    width: 100%;
  	}

  	#category-select-list{
				top: 60px;
  	}
} /* /.navFilterControls */

/* @end content filters */

.mura-grid {
			background: #f7f7f7;
			border: 1px solid #DBDBDB;
			border-bottom: 0;
			border-top: 0;
			float: none;
			font-size: 13px;
			margin-bottom: 16px;
			position: relative;

			.ui-draggable-dragging {
				.box-shadow(0px 0px 8px #666);
				}

			a {
				color: @grayDarker;
			}

			ol,ul {
				list-style: none;
				padding: 0;
      	margin: 0 0 @baseLineHeight / 2 0 !important;
			}

			ul.categorylist{
				margin-bottom: 0 !important;
			}

			li {
				clear: both;
				float: left;
				white-space: nowrap;
				margin: 0;
				padding: 0;
			}

			li:hover {
				cursor: move;
			}

			&.no-manual-sort li:hover {
				cursor: default;
			}

			/* Use this to highlight node on hover */
			li dl:hover {
				/*outline: 1px solid red;*/
			}

			li:hover #top-node, .mura-grid.noDrag li:hover  {
				cursor: auto;
			}

			> dl:last-child{
				border-bottom: none;
			}

			dl {
				background-color: @white;
				float: left;
				width: auto;
				margin: 0;
				border-bottom: 1px solid #dbdbdb;

				&.alt {
					background-color: @white;
				}

				&.mura-grid-hdr {
					background-color: #f4f3f3;
					border-bottom: none;
				}

				dt, dd  {
					color: #333232;
					// border-right: 1px solid #DBDBDB;
					text-align: center;
					float: left;
					margin: 0 !important;
					line-height: 42px;
					height: 42px;
				}

				dt {
					width: 405px;
					text-align: left;
					padding: 0 5px 0 0 !important;
					font-weight: normal;
					position: relative;
				}

				dd {
					padding: 0 5px;
					position: relative;
				}

				dt a {
					display: block;
					height: auto;
					min-height: inherit;
					line-height: inherit;
					white-space: wrap;
					padding: 0 5px 0 0;
					float: left;
					color: @grayDarker;
				}

				dt a.title,
				dt a.title-nested {
					overflow: hidden;
					position: relative;
					.text-overflow;

					&:hover { border-bottom: none; }
					&.locked {}

					&:before {
						position: relative;
						top: 2px;
						margin-left: 0;
						margin-right: 3px;
						font-size: 18px;
						line-height: 14px;
						color: @iconColor;
						display: inline-block;
						width: 22px;
						text-align: center;
						font-family: FontAwesome;
						}

					&.file {

						padding-left: 0;
						position: relative;

						&:before {
							background: @iconColor;
							border-radius:2px;
							color: @white;
							display: block;
							content: attr(data-filetype);
							float: left;
							font-family: 'SilkscreenNormal';
							font-size: 8px;
							left: 0;
							line-height: 10px;
							overflow: hidden;
							padding: 3px 4px 2px;
							position: relative;
							text-shadow: -1px -1px 0 rgba(0,0,0,.2);
							top: 14px;
							width: auto;
						}

						&.gif,
						&.png,
						&.jpg,
						&.jpeg {

							padding-left: 0;

							&:before {
								position: relative;
								top: 3px;
							}
						}
					}
				}
			}

			.gallery {

				.file.jpg:before,
				.file.jpeg:before,
				.file.png:before,
				.file.gif:before {
					.icon-font(FontAwesome);
					content: "\f03e";
					font-size: 19px;
					line-height: 18px;
					top: 6px !important;
					background: transparent;
					padding: 0;
					color: @iconColor;
					}
			}

			.restricted {

				outline: 1px dashed @orange;
				outline-offset: -1px;
				position: static;

				.restricted {
					outline: 0;

						&:before {
						content: "";
						background: transparent;
						padding: 0;
						width: 0;
						}

					}

				.marker {

						&:before {
						.icon-font('FontAwesome');
						display: block;
						content: "\f023";
						background: @orange;
						.square(24px);
						padding-top: 0;
						text-align: center;
						font-size: 16px;
						line-height: 24px;
						color: @white;
						.border-left-radius(4px);
						position: absolute;

						margin-left: -24px;
						margin-top: 0px;
						}
					}

				.restricted .marker { display: none; }
			}

			dt a.tooltip {
				padding: 0 10px 0 20px;
			}

			dt img {
				float: left;
				padding: 11px 5px 0 0;
				margin-left: -15px;
			}

			dt .tooltip img {
		    float: left;
		    padding: 0;
		    margin-left: 0;
		    border: 7px solid #333;
		    .border-radius(2px);
			}

			.hasChildren {
				float: left;
				.inline-block;
				width: 12px;
				height: 28px;
				line-height: 27px;
				padding: 0;
				margin-left: -15px;
				cursor: pointer;

				&:before {
					.icon-font('FontAwesome');
					color: @iconColor;
					font-size: 14px;
					margin-left: -12px;
					margin-top: 8px;
					padding: 0 12px;
					}

				&.open:before {
					content: "\f0d7"; // Down Caret Icon
					}

				&.closed:before {
					content: "\f0da"; // Right Caret Icon
					}
			}

			.order {
				width: 40px;
			}

			.objects {
				width: 60px;
			}

			.display, .feature {
				width: 60px;
			}

			.template {
				width: 70px;

					i {
						.square(18px);
						position: relative;
						top: 2px;

						}

				a.template-set { border: 0; }

				}

			.nav {
				width: 50px;
			}

			.updated {
				color: darken(@iconColor,10%);
				padding-left: 12px !important;
				text-align: left !important;
				width: 100px;
			}

			/*.date {
				width: 90px;
			}

			.time {
				width: 60px;
			}*/

			.actions {
				border-right: none !important;
				display:none;
				padding-left: 4px !important;
				width: 100px;
				}

			dt .add {
				width: 16px;
				margin: 0;
				margin-right: 7px;
				display: block;
				background: none;
				padding: 4px;
				border: none;
			}

			.mura-grid-hdr dt,
			.mura-grid-hdr dd {
				.bold-font;
				border: 1px solid #DBDBDB;
				border-right: none;
				color: @textcolor;
