	    z-index: 20;

	    	ul.actions-list{
	    		float: left;
	    		width: 100%;

		    	li{
		    		clear: both !important;
		    		float: left !important;
		    		list-style: none;
				    margin: 0;
				    padding: 0;
				    position: relative;
				    text-align:left;
				    width: 100%;

				    &+li.delete{
		    	    .mura-nav-divider;
				    }

			    	a{
					    display: block;
					    text-decoration: none;
					    margin: 0;
					    padding: 3px 12px;
					    line-height: 24px;
					    color: @textcolor;

					    i{
					    	margin-right: 8px;
					    }

						&.disabled {
							color: #999;
							border-color: transparent;
							pointer-events: none
						}
			    	}
	    		}

				li > a:hover {
					color: #545454;
					text-decoration: none;

					i {
						 color: #545454;
						}

					}

	    	}
	    	// /.actions-menu
	}
	// /.actions

		}

	.bullet { color: @iconColor;
		font-size: 20px;
		 }

	.add i {
			display: block;
			width: 24px;
			font-size: 16px;
			color: @iconColor;
			}

	.add:hover i {
			color: @grayDarker;
			}

	td {
    padding: 11px 8px 8px!important;
		border-left: 1px solid #DBDBDB;
		white-space: nowrap;
		width: 1%;

		&.add {

			padding: 5px 4px 0;

