		/*
	text-shadow: 0 0 1px @blue;
		text-stroke: 1px solid @white;
	*/

		}

	i+span, .bullet+span { display: none; } // Hiding <span> created for screen readers just in case the "title" isn't sufficient on a non-semantic element (i)

	i {
