/* License goes here */

@tbformheight: ~"calc(100vh - 380px)";
@tbformpagesw: 60px;
@tbformfieldsw: 230px;
@tbfieldsettingsw: ~"calc(100% - 290px)";
	
#mura-templatebuilder{

 	.ui-button-text {
		height: 32px;
		line-height: 32px;
		padding: 0 10px;
	}

	ul{
		margin: 0;
		padding: 0;
	}

	.tb-fieldIsEmpty {
		border: 1px solid #ff0000 !important;
	}	

	#mura-tb-form-fields {
		width: @tbformfieldsw !important;
	}
	#mura-tb-fields-settings {
		width: @tbfieldsettingsw !important;
	}

	#mura-tb-form-pages {
		background: #aaa;
		color: #fff;
		float: left;
		height: @tbformheight;
		overflow-x: hidden;
		overflow-y: auto;
		padding: 10px 4px 4px 8px;
		text-align: center;
		width: @tbformpagesw;
	}
	#mura-tb-form-pages .current {
		background: #ccc;
		position: relative;
	}	
	#mura-form-pages li,
	#mura-form-addpage {
		padding: 16px 4px 14px 5px;
		margin: 8px 0;
		background: #fff;
		color: @textcolor;
		border: none;
		.border-radius(2px);
		list-style: none;
		list-style-type: none;
		cursor: pointer;
		width: @tbformpagesw - 16px !important;
	}

	#mura-form-addpage {
		padding: 2px 4px 5px 5px;
		font-size: 2em;
		background: #fff;
	}

 	.button-trash {
		.border-radius(0);
		.box-shadow(~"0 0 0,0 0 0") !important;
		background-image:none !important;
		border: 0 !important;
		display: block;
		height: 16px;
		width: 16px;
		margin: 0;
		padding: 0;
 		position: absolute;
	 		top: 11px;
 			right: 5px;
		text-shadow: 0 0 0 !important;
	}

	.button-trash:before{
			font-family: FontAwesome,@sansFontFamily;  
		  content: "\f057";
	}


	#mura-form-pages .button-trash{
		margin: -12px -4px 0 0;
	}

}
