.ui-dialog{
 background-position: left bottom;
 background-repeat: no-repeat;
 background-size: 240px 240px;
 box-shadow: 0px 20px 50px 0px rgba(0,0,0,0.30);
 padding: 26px;
 /* spinner #action-modal z-index is 1040 */
 z-index: 1039;

 &.dialog-confirm{
  background-image:url(../images/dialog-confirm-bg@2x.png);
 }
 &.dialog-info{
  background-image:url(../images/dialog-info-bg@2x.png);
 }
 &.dialog-warning{
  background-image:url(../images/dialog-warning-bg@2x.png);
 }
 &.dialog-nobg{
  background-image:none !important;
 }

  &[aria-describedby=cropper]{
    background-image: none;
  }

  &[aria-describedby=changesetContainer] {
      left: 50% !important;
      min-width: 350px !important;
      width: auto !important;
      height: fit-content;
      top: 50%;
      transform: translate(-50%, -50%) !important;
      min-height: 0px !important;
  }
  &[aria-describedby="newContentMenuContainer"] {
      max-height: ~'calc(100% - 84px)' !important;
      overflow-y:auto !important;
  }

  #changesetContainer td {
    width: auto;

    &.var-width{
      max-width: none;
      text-overflow: clip;
      overflow: visible;      
    }

  }

} // ui-dialog

  
.ui-dialog h1:not([class]),
.ui-dialog-title,
.modal-wrapper .ui-dialog-title{
  .bold-font;
  color: #545454;
  display: block;
  float: left; 
  font-size: 24px;
  line-height: 1.2;
  width: 100%;
}

.ui-dialog #draft-prompt>p:first-child {
    font-size: auto;
}
.modal-wrapper .ui-dialog-content{
  background-color: #fff;
  border-radius:2px;
}

#changesetContainer{
  background-color: #fff;
  padding: 0;
  .border-radius(0);
}

.ui-dialog .ui-dialog-content{
  margin-top: 0 !important;
  overflow: visible !important;
  padding: 12px 0;
  background: none;
  background-color: transparent;

  &#newFileMetaContainer{
    padding-top: 12px;
    background-color:transparent;

    .block{
      margin-bottom:-20px;
    }
  }

  .mura-header ul.breadcrumb{
      background-color: #FAFAFA;
      border-radius: 0;
      border: 1px solid #eaeaea;
      padding: 4px 12px;
  }

} // ui-dialog-content


.ui-dialog{
  font-size: 14px;

   h1{
    color: @text-color !important;
    text-align: left;
    margin: 0 !important;
    padding: 0 !important;
  }  

  .mura-header{
    margin: -9px 0 12px;
  }
  
  label{
    font-size: 14px;
    font-weight: 700;
      > span{
        font-weight: 400;
      }

  }

  .block,
  .block-content,
  .block-constrain,
  .block-bordered{
    background-color: transparent !important;
    box-shadow: none !important;
    margin: 0 !important;
    padding:0 !important;
  }

}

.ui-dialog .buttonset,
.ui-dialog .ui-dialog-buttonpane{
  margin: 26px 0 8px;
}
.ui-dialog .buttonset{  
    text-align: right;
    margin-top: 58px;
}
.ui-dialog .ui-buttonset,
.ui-dialog .ui-dialog-buttonpane .ui-dialog-buttonset{
  float:right;
  text-align: right;
}

.ui-dialog .buttonset button,
.ui-dialog .ui-dialog-buttonpane .ui-dialog-buttonset .ui-button{
  float:none;
  margin-left: 13px;
  background-color: @mura-red !important;
  background-position: 0 0 !important;
  border-radius: 3px !important;
  border: none;
  color: #fff !important;
  font-weight: 400;
  padding: .4em 1.1em;

  &.mura-primary{
   background-color: @mura-red;
   color: #fff !important;
   }

  &.mura-secondary{
   background-color: #6f6f6f !important;
   color: #fff !important;
   }

   > i{
    margin-right: 6px;
   }
}

.ui-dialog-buttonset .ui-button-text-only .ui-button-text {
  font-weight: normal;
  color: #fff;
}

.ui-dialog .buttonset button:hover,
.ui-dialog .ui-dialog-buttonpane .ui-dialog-buttonset .ui-button.ui-state-hover{
  background-color: #DDDEDD;
  background-position: 0 0;
}
.ui-dialog .buttonset .ui-button:hover,
.ui-dialog .ui-dialog-buttonpane .ui-dialog-buttonset .ui-button .ui-state-hover{
  background-color:#FFF;
}

/* prevent unwrapped text from showing next to dialog close icon */
.ui-dialog .ui-dialog-titlebar-close{
    height: 16px;
    width: 16px;
    overflow: hidden;
}

.ui-dialog .ui-dialog-titlebar-close,
.ui-dialog .ui-dialog-titlebar-close:focus,
.ui-dialog .ui-dialog-titlebar-close:hover,
.ui-dialog .ui-dialog-titlebar-close:before,
.ui-dialog .ui-dialog-titlebar-close:hover:before{
  color:#949494 !important;
  opacity: .65 !important;
}

.ui-dialog .ui-dialog-titlebar-close:before{
  font-size: 18px;
}
#cropper .jcrop-holder{
  margin-left: 8px;
}

#ui-datepicker-div{
  .border-radius(0) !important;
  z-index: 555 !important;
}

.ui-widget-overlay { 
  position: fixed; 
  top: 0; 
  left: 0; 
  width: 100%; 
  height: 100%; 
  background-image:none !important;
  background-color: #fff !important;
  opacity: .7 !important;
}

.modal-backdrop{
  background-image:none !important;
  background-color: #fff !important;
  opacity: .7 !important;
}

/* these styles are used in file browser dialogs - check all views before changing!! */
.modal-wrapper{
  
  .ui-dialog{
    padding: 15px 30px !important;
    background-color: #fff;
    min-height: 100% !important;
    width: auto;

    .mura-header{
      margin-top: 0 !important;
    }


    .buttonset{
      margin-top: 28px;
    }
  }

  /* direct child is on form and component front end editing e.g. title required */
  > .ui-dialog{
    min-height: 0 !important;
  }
  
  .ui-dialog .ui-dialog-titlebar-close,
  .ui-dialog .ui-dialog-titlebar-close:focus,
  .ui-dialog .ui-dialog-titlebar-close:hover{
  top: 2px;
  right: 0px !important;
  height: 18px;
  width: 16px;
  opacity: .65;
  }
  .ui-dialog .ui-dialog-titlebar-close:before{
    font-size: 18px;
    margin-top: 0 !important;
    margin-right: 0 !important;
    margin-bottom: 0 !important;
    margin-left: 0 !important;
    top: 2px;
    right: 0;
  }
   .ui-dialog .ui-dialog-titlebar-close,
  .ui-dialog .ui-dialog-titlebar-close:before,
  .ui-dialog .ui-dialog-titlebar-close:focus,
  .ui-dialog .ui-dialog-titlebar-close:hover,
  .ui-dialog .ui-dialog-titlebar-close:hover:before{
    color: #545454 !important;
  }
}

/* autocomplete used for tags */
.ui-widget.ui-autocomplete{
  padding: .25em .25em;
}

.ui-autocomplete.ui-front {
  z-index:100000;
}

.ui-menu .ui-menu-item a,
.ui-menu .ui-menu-item a.ui-state-focus,
.ui-menu .ui-menu-item a.ui-state-active { 
  background: none;
  background-color: transparent;
  background-image: none;
  border: none;
  color: #333 !important;
  cursor: pointer;
  height: 1.6em !important;
  filter: none;
  padding-top: 0;
  padding-bottom: 0;
  text-shadow: none;

  &:hover{
    opacity: .8;
  }
}